.sidebar{
  background-color: #363740;
  height: 100vh;
  width: 20%;

  .top{
    margin: 20px 10px;
    display: flex;
    align-items: center;
    justify-content: center;

    .logo-img{
      margin-right: 12px;
    }

    .logo-title{
      font-style: normal;
      font-weight: bold;
      font-size: 19px;
      line-height: 24px;
      letter-spacing: 0.4px;
      color: #A4A6B3;
      opacity: 0.7;
    }

  }

  .menu{
    display: block;
    align-items: center;
  }

}